.page-link:focus {
  box-shadow: none; }

.headerSortDown:after,
.headerSortUp:after {
  content: ' ';
  position: relative;
  left: 10px;
  border: 7px solid transparent; }

.headerSortDown:after {
  top: 10px;
  border-top-color: silver; }

.headerSortUp:after {
  bottom: 15px;
  border-bottom-color: silver; }

.headerSortDown,
.headerSortUp {
  padding-left: 20px; }

.list-group-item.active-selected {
  z-index: 2;
  color: #212529;
  border-bottom: #007bff solid 2px !important; }
